//存放图片的地址(该目录下共存放300张图片0-300.jpg)
export function returnImg(number, min, max) {
  //产生不重复的随机数
  // 其中num是要生成随机数的个数，min,max一般是0和9
  // number你想取多少图片
  // min起始
  // max最大个数
  var getNumber = number
  var baseUrl = "https://mrblackant.github.io/allImgs/za" //存放图片的地址(改目录下共存放90张图片0-90.jpg)
  var arr = [];
  var temp = []; //temp存放生成的随机数组
  let i = 0;
  if (getNumber > 300) {
    // 用户选择了300+张图片，不考虑重复的问题
    for (var k = 0; k < getNumber; k++) {
      var nameIndex2 = parseInt(Math.random() * max)
      temp.push(baseUrl + nameIndex2 + '.jpg'); //获取arr[num]并放入temp
    }
  } else {
    do {
      var nameIndex = parseInt(Math.random() * max)
      if (arr.indexOf(nameIndex) < 0) {
        arr.push(nameIndex)
        temp.push(baseUrl + nameIndex + '.jpg'); //获取arr[num]并放入temp
        i++;
      }
    }
    while (i < getNumber);
  }

  return temp;
}
// 生成排好大小顺序的随机数
export function returnNoreapt(number, min, max) {
  // 其中num是要生成随机数的个数，min,max一般是0和9
  // number你想取多少图片
  // min起始
  // max最大个数
  var arr = [];
  for (var i = min; i < max; i++) {
    arr.push(i)
  }
  var temp = []; //temp存放生成的随机数组
  var count = arr.length;
  for (i = 0; i < number; i++) {
    var num = Math.floor(Math.random() * arr.length); //生成随机数num
    temp.push(arr[num]); //获取arr[num]并放入temp
    arr.splice(num, 1);
  }
  // 从小到大排序
  temp.sort(function(x, y) {
    return x - y;
  });
  return temp;
}


