export function returnName(number, min, max) {
  let arr = []; //tempName存放生成的随机数组
  var i = 0;
  do {
    let getSjName = getName()
    if (arr.indexOf(getSjName) < 0) {
      arr.push(getSjName)
      i++;
    }
  }
  while (i < number);
  return arr;
}

