<template>
  <div class="app_chose_time_wapper">
    <!-- 选择时间的弹窗 -->
    <div class="app_pop_inner">
      <div class="">
        填写时间
        <mt-field label="" v-model="initComTime">
          <mt-button class="can_diy" type="primary" size="small" @click="changeType">切换格式</mt-button>
        </mt-field>
      </div>
      <div class="tips">
        <span>时间可以手动填写，一般是以下3种类型</span>
        <template v-if="ifIndex">
          <span>类型1: 3分钟前</span>
          <span>类型2: 3小时前</span>
          <span>类型3: 昨天</span>
        </template>
        <template v-else>
          <span>类型1: 12:00</span>
          <span>类型2:前天12:00</span>
          <span>类型3: 2020年10月1日 10:00</span>
        </template>
      </div>
      <div class="btn_wapper">
        <mt-button type="primary" size="small" @click="postForm">确定</mt-button>
        <mt-button size="small" type="danger" @click="delForm">取消</mt-button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    initTime: {
      type: String,
      default: ''
    },
    ifIndex: {
      type: Boolean,
      default: false
    }
  },
  components: {},
  data() {
    return {
      initNum: 0,
      initComTime: this.initTime,
      rememberTime: JSON.parse(JSON.stringify(this.initTime))
    }
  },
  watch: {
    initTime() {
      this.initComTime = this.initTime
    }
  },
  mounted() {},
  methods: {
    addImg() {},
    postForm() {
      // 保存
      this.$emit('upDateTime', this.initComTime)
    },
    delForm() {
      // 取消
      this.$emit('close')

    },
    changeType() {
      this.initNum += 1
      if (this.initNum > 3) { this.initNum = 1 }
      // 改变类型
      let baseData = this.ifIndex ? {
        '1': '3分钟前',
        '2': '3小时前',
        '3': this.rememberTime
      } : {
        '1': '10:00',
        '2': '昨天10:00',
        '3': this.rememberTime
      }
      // 修改显示数据
      this.initComTime = baseData[this.initNum]
    }
  }
};

</script>
<style scoped lang="scss">
.app_chose_time_wapper {
  .app_pop_inner {
    padding: 20px;

    .btn_wapper {
      text-align: center;
    }

    .tips {
      margin-top: 10px;
      color: #bcb9c0;
      font-size: 33px;

      span {
        display: block;
        margin-bottom: 10px;
      }
    }
  }
}

</style>



