<template>
  <div class="upload_com_wapper" :class="boxClass?boxClass:''">
    <!-- 上传头像组件 -->
     <input type="file" id="files" @change="filesChange($event)"  name="files"  accept="image/*" :multiple="ifMultiple"  style="display: none;" />
  </div>
</template>
<script>
import { upload } from "@/utils/makeRotaeImg.js";
import { getPhone } from "@/utils/index.js";

var path = ''
export default {
  props: {
    ifM: {
      // 是否多选
      type: Boolean,
      default: false
    },
    needWidHei: {
      // 是否需要提供照片的宽高
      type: Boolean,
      default: false
    },
    fatherClass: {
      type: String,
      default: 'upload_com_wapper'
    }
  },
  data() {
    return {
      ifPg: false, //手机类型
      ifMultiple: this.ifM,
      boxClass: this.fatherClass, //动态的class
    }
  },
  watch: {
    ifM() {
      // 控制单选还是多选图片
      this.ifMultiple = this.ifM
    },
    boxClass() {
      // 给wapper设置动态class
      this.boxClass = this.fatherClass
    }
  },
  mounted() {
    // 判断用户手机类型
    this.ifPg = getPhone() && getPhone() === 'pg' ? true : false
    console.log('ifPg= ' + this.ifPg)
  },
  methods: {
    actions() {
      // 触发上传
      path = '.' + this.boxClass
      $(path).find("#files").click()
    },
    pgUpload(item, vm, imgArr, files) {
      console.log('苹果上传')
      // 如果是iphone用户的上传，将图片旋转矫正
      upload(item, function(base64) {
        let pp = vm.getUrl(base64)
        imgArr.push(pp)
        if (imgArr.length === files.length) {
          // 全部矫正完毕,赋值渲染
          vm.givenImgs(imgArr)
        }
      })
    },
    filesChange(e) {
      //获取file文件
      var imgArr = [],
        vm = this;
      // let files = e.target.files || e.dataTransfer.files;
      var files = $(path).find("#files").get(0).files
      console.log(files)
      if (files.length <= 0) return;
      for (var i = 0; i < files.length; i++) {
        if (this.ifPg) {
          this.pgUpload(files[i], vm, imgArr, files)
        } else {
          imgArr.push(this.getUrl(files[i]))
        }
      }
      if (!this.ifPg) {
        // 安卓用户，将上方遍历的结果返回
        console.log('安卓上传')
        this.givenImgs(imgArr)
      }
    },
    givenImgs(imgArr) {
      if (this.needWidHei) {
        let _that=this
        // 需要得到用户图片的宽高
        let img = new Image(); //手动创建一个Image对象
        img.src = imgArr[0]; //创建Image的对象的url
        img.onload = function() {
          console.log('height:' + this.height + '----width:' + this.width)
          _that.$emit('setImg', imgArr, this.width, this.height)
        }
      } else {
        // 直接传递图片
        this.$emit('setImg', imgArr)
      }
      // 然后将自己清空
      $(path).find("#files").val(null)
    },
    getUrl(file) {
      var url = null;
      if (window.createObjectURL != undefined) {
        url = window.createObjectURL(file)
      } else if (window.URL != undefined) {
        url = window.URL.createObjectURL(file)
      } else if (window.webkitURL != undefined) {
        url = window.webkitURL.createObjectURL(file)
      }
      return url
    },
  }
};

</script>
<style scoped lang="scss">
.upload_com_wapper {}

</style>



