<template>
  <div class="make_likes_wapper">
    <div class="make_img_wapper">
      <div class="imgs_show_wapper" v-if="!ifOnlyText">
        <!-- 遍历展示图片 -->
        <template v-for="(item,index) in getImgs">
          <img class="like_img" :key="item+index" :src="item" @click="delImg(item,index)" />
        </template>
      </div>
      <div class="control_wapper">
        <!-- 上传 btn-->
        <div v-if="!ifOnlyText" class="nr_div_wapper common_back" id="nr_div_wapper" @click="changeContentImg"></div>
        <template v-else>
          <div class="fake_inp_z">
            <!-- 输入框 -->
            <mt-field v-model="fakeImgsZ" label="" placeholder="输入昵称,','号分割" type="textarea" rows="3">
              <mt-button type="primary" size="small" @click="sjOnez">随机1条</mt-button>
            </mt-field>
          </div>
        </template>
        <!-- 生成按钮 -->
        <div class="inner_make_btn">
          <mt-button type="primary" size="small" @click="postForm">保存</mt-button>
          <mt-button size="small" type="danger" @click="delForm">清空</mt-button>
          <mt-button type="primary" plain size="small" @click="backBtn">返回</mt-button>
          <div class="show_all_len">当前共{{onlyText?fakeImgsZ.split('，').length:getImgs.length}}条点赞</div>
          <div class="">在页面能承受的前提下可以增加很多的赞,避免不了会有重复的,可以手动删除</div>
        </div>
      </div>
    </div>
    <!-- 上传组件 -->
    <common-upload ref="loadImg" :fatherClass="'commonLikes'" key="commonLikes" :ifM="true" @setImg="setImg"></common-upload>
    <!-- 上传还是数据库增加 -->
    <mt-popup v-model="popupVisible4" class="app_pop_wapper">
      <div class="app_pop_inner">
        <div class="pop_item pop_item_center">当前共{{getImgs.length}}条点赞</div>
        <div class="pop_item" @click="addImg('local')">相册上传</div>
        <!--   <div class="pop_item" @click="addImg('sj1')">随机1条(自动去重)</div>
        <div class="pop_item" @click="addImg('sj2')">随机20条(自动去重)</div> -->
        <div class="picker_wapper">
          <span class="picker_title">选择图片种类:</span>
          <mt-picker :slots="yearSlot" @change="onYearChange" :visible-item-count="3"></mt-picker>
        </div>
        <div class="pop_item">
          <!--   <input type="number">
          条(自动去重)</div> -->
          <mt-field v-model="controlNum" label="随机n张:" placeholder="请输入数字" type="number">
            <mt-button class="can_little_pad" type="primary" size="small" @click="checkBtn">去重添加</mt-button>
          </mt-field>
        </div>
      </div>
    </mt-popup>
  </div>
</template>
<script>
import commonUpload from '@/components/commonUpload2'
import { MessageBox } from 'mint-ui';
import { allImgs } from "@/utils/allImgs.js"
import { returnImg, returnName, returnNoreapt } from "@/utils/makeJson.js"
export default {
  props: {
    onlyText: {
      // 是否只展示文字赞
      type: Boolean,
      default: false
    },
    initLikesText: {}
  },
  data() {
    return {
      ifOnlyText: this.onlyText,
      getImgs: [],
      fakeImgsZ: '',
      controlNum: 20,
      popupVisible4: false,
      formAn888: false,
      pickerVal: '女生',
      yearSlot: [{
        flex: 1,
        values: ['女生', '男生', '卡通', '搞笑','风景', '杂'],
        className: 'slot1'
      }],
    }
  },
  components: { commonUpload },
  watch: {
    onlyText() {
      this.ifOnlyText = this.onlyText
    },
    initLikesText() {
      this.fakeImgsZ = this.initLikesText ? this.initLikesText.join('，') : ''
    }
  },
  mounted() {},
  methods: {
    sjOnez() {
      // 随机增加赞
      let p = returnName(1)
      let getBase = JSON.parse(JSON.stringify(this.getImgs))
      this.getImgs = Array.from(new Set(getBase.concat(p)))
      // 替换逗号
      let newP = JSON.parse(JSON.stringify(this.getImgs))
      newP = newP.join(",").replace(/,/g, '，')
      this.fakeImgsZ = newP
      console.log(newP)
    },

    onYearChange(picker, values) {
      // picker change事件
      this.pickerVal = values
      // 从自己的系统中取图片
      this.formAn888 = this.pickerVal[0] === '杂' ? true : false
      console.log(this.formAn888)
    },
    checkBtn() {
      // 添加btn
      if (this.formAn888) {
        // 从an888系统中取片
        this.getImgs = Array.from(new Set(this.getImgs.concat(returnImg(this.controlNum, 0, 300))))
        return
      }
      let p = returnNoreapt(this.controlNum, 0, allImgs[this.pickerVal].length)
      let getImgLink = []
      p.forEach(item => {
        getImgLink.push(allImgs[this.pickerVal].baseImgUrl+item+'.jpg')
      })
      // 拼装
      this.getImgs = Array.from(new Set(this.getImgs.concat(getImgLink)))
    },
    setImg(urls) {
      // 从组件那里获取到头像
      this.getImgs = this.getImgs.concat(urls)
    },
    addImg(type) {
      // 增加点赞
      switch (type) {
        case 'local':
          // 本地上传
          this.$refs.loadImg.actions()
          break;
        case 'sj1':
          // 随机1张
          this.getImgs = Array.from(new Set(this.getImgs.concat(returnImg(1, 0, 300))))
          break;
        case 'sj2':
          // 随机20张
          this.getImgs = Array.from(new Set(this.getImgs.concat(returnImg(20, 0, 300))))
          break;
      }
    },
    delImg(item, index) {
      // 删除图片
      MessageBox.confirm('确认删除图片吗?').then(action => {
        // 删除图片
        this.getImgs.splice(index, 1)
      }, cancel => {});
    },
    changeContentImg() {
      this.popupVisible4 = true
    },
    delForm() {
      // 清空图片
      MessageBox.confirm('确认清空吗?').then(action => {
        // 清空图片
        this.getImgs = []
        this.fakeImgsZ = ''
      }, cancel => {});
    },
    postForm() {
      // 保存
      this.$emit('close')
      this.$emit('getLikesImg', this.onlyText ? this.fakeImgsZ.split('，') : this.getImgs)
    },
    clearForm() {
      // 父组件的清空
      this.getImgs = []
    },
    backBtn() {
      // 返回
      this.$emit('close')
    }
  }
};

</script>
<style scoped lang="scss">
.make_likes_wapper {
  height: 100%;

  .nr_div_wapper {
    width: 128px;
    height: 128px;
    display: inline-block;
    margin-left: 60px;
    background-image: url('../assets/addImg.jpeg');
    margin-bottom: 15px;


  }

  .fake_inp_z {
    border-bottom: 1px solid #e6e6e6;
    margin-bottom: 30px;
  }

  .make_img_wapper {
    width: 100%;
    height: 100%;
    // background: #fff;
    // padding: 30px 0 15px 0;
  }

  .inner_make_btn {
    text-align: center;
  }

  .like_img {
    width: 128px;
    border-radius: 10px;
    height: 128px;
    display: inline-block;
    margin-right: 15px;
    margin-bottom: 15px;
  }

  .show_all_len {
    margin-top: 10px;
  }

  .imgs_show_wapper {
    max-height: 70%;
    box-sizing: border-box;
    padding-left: 60px;
    overflow: auto;
    background: #fff;
    padding-top: 30px;
    margin-bottom: 25px;
  }

  .control_wapper {
    background: #fff;
    padding: 30px 0;
  }

  .picker_wapper {
    position: relative;

    .picker_title {
      font-weight: bold;
      position: absolute;
      color: #fff;
      top: 45px;
      left: 5px;
      z-index: 99;
    }
  }

  .miniBtn {
    // font-size: 14px;
  }
}

</style>



