<template>
  <div class="make_pls_wapper">
    <div class="make_form_wapper makeInfo_wapper">
      <!-- 头像 -->
      <mt-field v-if="!onlyText" class="tx_wapper" disabled label="评论人头像" placeholder="" v-model="badInp">
        <div class="tx_div_wapper common_back hasBg" id="tx_div_wapper" @click="changeHeadImg"></div>
      </mt-field>
      <!-- 昵称 -->
      <mt-field label="评论人昵称" placeholder="必填" v-model="plForm.nickName">
        <mt-button type="primary" class="sj_btns" size="small" @click="sjPl">随机</mt-button>
      </mt-field>
      <!-- 评论对象昵称 -->
      <mt-field v-if="onlyText" label="评论对象昵称" placeholder="必填" v-model="plForm.plDx">
      </mt-field>
      <!-- 昵称 -->
      <mt-field v-if="!onlyText" label="评论时间" placeholder="" disabled>
        <div class="fake_show_time" :class="plForm.time?'':'fake_inp_pla'" @click="getSetTime">{{plForm.time?plForm.time:'点击填写时间'}}</div>
      </mt-field>
      <!-- 评论内容 -->
      <mt-field class="pl_inner" type="textarea" label="评论内容" placeholder="" v-model="plForm.word" placeholder="评论文字内容" rows="4"></mt-field>
    </div>
    <!-- 生成按钮 -->
    <div class="inner_make_btn">
      <mt-button type="primary" size="small" @click="postForm">保存</mt-button>
      <mt-button size="small" type="danger" @click="delForm">清空</mt-button>
      <mt-button type="primary" plain size="small" @click="backBtn">返回</mt-button>
    </div>
    <!-- 上传组件 -->
    <common-upload ref="loadImg" key="makePl" :fatherClass="'makePl'" :ifM="false" @setImg="setImg"></common-upload>
    <!-- 评论头像选择 -->
    <mt-popup v-model="popupVisible7" class="app_pop_wapper">
      <div class="app_pop_inner">
        <div class="pop_item" @click="addImg('local')">相册上传</div>
        <div class="pop_item" @click="addImg('sj1')">随机1条</div>
      </div>
    </mt-popup>
    <!--选择时间日期的组件 -->
    <mt-popup v-model="setTimePop" class="app_pop_wapper">
      <chose-time :initTime="plForm.time" @upDateTime="(data)=>{plForm.time=data,setTimePop=false}" @close="setTimePop=false"></chose-time>
    </mt-popup>
  </div>
</template>
<script>
import choseTime from '@/components/choseTime'
import commonUpload from '@/components/commonUpload2'
import { MessageBox } from 'mint-ui';
import { returnImg, returnName, returnNoreapt } from "@/utils/makeJson.js"
import {
  GetDateAndHourStr
} from '@/utils'
export default {
  props: {
    sendNick: {
      // 昵称
      type: String,
      default: ''
    },
    ifUseItem: {
      // 是否使用传来的数据展示评论
      type: Boolean,
      default: false
    },
    onlyText: {
      // 是否只展示文字
      type: Boolean,
      default: false
    },
    plData: {
      // 传来的评论内容item
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      badInp: '',
      popupVisible7: false,
      ifUseIpl: this.ifUseItem,
      setTimePop: false,
      plForm: {
        img: '',
        nickName: '',
        time: GetDateAndHourStr(),
        word: '',
        plDx: this.sendNick, //评论对象昵称
      }
    }
  },
  components: { commonUpload, choseTime },
  watch: {
    ifUseItem() {
      this.ifUseIpl = this.ifUseItem
    },
    sendNick() {
      // 用户昵称
      this.plForm.plDx = this.sendNick
    },
    plData() {
      console.log(this.ifUseIpl)
      console.log(this.plData)
      // 是否展示用户当前的那条评论
      if (this.ifUseIpl) {
        this.plForm = this.plData
      }
    }
  },
  mounted() {
    console.log(this.sendNick)
  },
  methods: {
    sjPl() {
      // 随机评论
      this.plForm.nickName = returnName(1)[0] ? returnName(1)[0] : ''
    },
    getSetTime() {
      // 选择时间组件显示
      this.setTimePop = true
    },
    backBtn() {
      // 返回
      this.$emit('close')
    },
    setImg(urls) {
      console.log(urls)
      // 头像图片显示
      this.plForm.img = urls[0]
      $("#tx_div_wapper").css({
        'background-image': 'url(' + urls[0] + ')'
      })
    },
    addImg(type) {
      // 增加点赞
      switch (type) {
        case 'local':
          // 本地上传
          this.$refs.loadImg.actions()
          break;
        case 'sj1':
          // 随机1张
          this.setImg(returnImg(1, 0, 300))
          break;
      }
      // 渲染完，弹窗关闭
      this.$nextTick(() => {
        this.popupVisible7 = false
      })
    },
    changeHeadImg() {
      // 头像图片
      this.popupVisible7 = true
    },
    postForm() {
      // 关闭当前
      this.$emit('close')
      // 返回数据
      this.$emit('getAllPl', JSON.parse(JSON.stringify(this.plForm)))
    },
    delForm() {
      // MessageBox.confirm('确认清空吗?').then(action => {
        // 清空
        Object.keys(this.plForm).forEach(item => {
          if (item === 'plDx') {
            // 初始评论对象昵称
            this.plForm[item] = this.sendNick
          } else {
            this.plForm[item] = ''
          }
        })
      // }, cancel => {})
    },
  }
};

</script>
<style scoped lang="scss">
.make_pls_wapper {
  .inner_make_btn {
    padding: 30px 0;
    text-align: center;
    background: #fff;
  }

  .tx_wapper {
    height: 188px;
  }

  .tx_div_wapper {
    width: 128px;
    height: 128px;

    &.hasBg {
      background-image: url('../assets/nodoby.jpg')
    }
  }

  .pl_inner {
    margin-top: 20px;
  }

  .sj_btns {
    margin-left: 10px;
  }

  .fake_inp_pla {
    color: #757575;
  }
}

</style>



